:root {
	--primary-color: #333;
	--secondary-color: #444;
}

* {
	margin: 0;
	padding: 0;
}

.instruction_wrapper {
	font-family: 'Catamaran', sans-serif;
	line-height: 1.6;
	color: #333;
	font-size: 1.1rem;
}

h1,
h2,
h3,
h4 {
	line-height: 1.3;
}

a,
.link {
	color: #444;
	text-decoration: none;
}

ul {
	list-style: none;
}

img {
	width: 100%;
}

.container {
	max-width: 1100px;
	margin: auto;
	overflow: hidden;
	padding: 0 2rem;
}

.navbar {
	font-size: 1.2rem;
	padding-top: 0.3rem;
	padding-bottom: 0.3rem;
	background-color: white;
}

.navbar .container {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: 2rem;
}

.navbar .logo {
	font-size: 2rem;
	line-height: 1.3;
}

.logo {
	font-weight: bold;
}

.navbar ul {
	justify-self: flex-end;
	display: flex;
	align-items: center;
	justify-content: center;
}

.navbar a {
	padding: 0 1rem;
}

.navbar a:hover {
	color: #555;
}

.section-a {
	margin: 2rem 0;
}

.section-a .container {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 3rem;
	align-items: center;
	justify-content: center;
}

.section-a h1 {
	font-size: 4rem;
	color: var(--primary-color);
}

.section-a p {
	margin: 1rem 0;
}

.section-b {
	position: relative;
	height: 600px;
	background-color: #cecece;
}

.section-b-inner {
	box-sizing: border-box;
	color: #fff;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	margin: auto;
	max-width: 860px;
	padding: 5rem 0;
}

.section-b-inner h3 {
	font-size: 2rem;
}

.section-b-inner h2 {
	font-size: 5rem;
	margin-top: 1rem;
}

.section-b-inner p {
	margin-top: 1rem;
	font-size: 1.5rem;
}

.section-c .gallery {
	display: grid;
	grid-template-columns: repeat(5, 1fr);
}
.section-c .gallery a:first-child {
	/* grid-row-start: 1;
  grid-row-end: 3; */
	grid-row: 1/3;
	grid-column: 1/3;
}

.section-c .gallery a:nth-child(2) {
	grid-column-start: 3;
	grid-column-end: 5;
}

.section-c .gallery img,
.section-c .gallery a {
	width: 100%;
	height: 100%;
}

.section-footer {
	background: var(--primary-color);
	color: #fff;
	padding: 4rem 0;
}

.section-footer .container {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: 1rem;
}

.section-footer h2 {
	font-size: 2rem;
	margin-bottom: 1rem;
}

.section-footer h3 {
	margin-bottom: 0.7rem;
}

.section-footer a {
	line-height: 1.9;
	color: #ccc;
}

.section-footer a > i {
	color: #555;
	margin-right: 0.5rem;
}

.email-form {
	width: 100%;
	display: inline-block;
	background-color: #555;
	position: relative;
	border-radius: 20px;
	line-height: 0;
	margin-top: 1rem;
}

.icon {
	font-size: 3rem;
	padding: 10px;
}

.icon:hover {
	color: grey;
}

.btn {
	display: inline-block;
	background: var(--primary-color);
	color: #fff;
	padding: 0.8rem 1.5rem;
	border: none;
	cursor: pointer;
	font-size: 1.1rem;
	border-radius: 30px;
}

.btn:hover {
	background: var(--secondary-color);
}

.about-me {
	box-sizing: border-box;
	background-color: #f8f9fa;
	padding-top: 0.25rem;
	padding-bottom: 0.25rem;
}

.aboutme_container {
	padding-right: 3rem;
	padding-left: 3rem;
}

.about_content {
	display: flex;
	justify-content: center;
}

.about_wrapper {
	background-color: #f8f9fa;
	padding: 50px;
	text-align: center;
}

.about_content {
	flex: 3;
}

#profilepic {
	height: 200px;
	width: auto;
	border-radius: 50%;
}

@media (max-width: 700px) {
	.section-a .container {
		grid-template-columns: 1fr;
		text-align: center;
	}

	.section-a .container div:first-child {
		order: 2;
	}

	.section-a .container div:nth-child(2) {
		order: -1;
	}
}

@media (min-width: 900px) {
	.aboutme_container {
		max-width: 960px;
	}
}

@media (min-width: 768px) {
	.aboutme_container {
		max-width: 720px;
	}
}

@media (min-width: 576px) {
	.aboutme_container {
		max-width: 540px;
	}
}

@media (max-width: 570px) {
	.aboutcontent_wrapper {
		flex-direction: column;
	}
	.aboutcontent_wrapper img {
		max-height: 300px;
		width: auto;
	}

	.about_photo {
		display: flex;
		justify-content: center;
	}
}
